const NavBar = (props) => {
    const { classes } = props;
    return (
        <header>
            <nav>
                <div>icon</div>
                <div>{options.title}</div>
                <div>
                    <ul>
                        {options.sections.map((section) => (<li>{section}</li>))}
                    </ul>
                </div>
            </nav>
        </header>
    )
}

export default withStyles(styles)(NavBar);